<template>
  <div id="app">
    <h1>翻译</h1>
    <h5>简单，易用，便捷</h5>
   <translateFrom v-on:formSubmit="translateText"/>
  <translateOutPut v-text="translateText1"/>
  </div>
</template>

<script>
import TranslateFrom from "./components/translateFrom";
import TranslateOutPut from "./components/translateOutPut";

export default {
  name: "App",
  data:function(){
    return{
      translateText1:""
    }
  },
  components: {
    TranslateFrom,TranslateOutPut

  },
  methods: {
    translateText: function(text,lang) {
      this.$http
        .get(
          "https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180312T082701Z.431d7051335e8535.75bfb279db03d4e5c0327f8552910dcbbd916067&lang="+lang+"&text=" +
            text
        )
        .then((res )=> {
          console.log(res);
         this.translateText1=res.body.text[0];
        });
    }
  }
};
</script>

<style scoped>

</style>



